.playerPanel {
  height: 100%;
  width: 100%;
  position: relative;
  display: flex;
  align-items: center;
  transition: opacity 0.3s;
  opacity: 0;
  color: rgba(225, 225, 225, 0.8);
  margin: 0 -8px;

  &.show {
    opacity: 1;
  }

  &.disabled {
    &:after {
      display: block;
      content: '';
      position: absolute;
      width: 100%;
      height: 100%;
      cursor: not-allowed;
    }
  }

  svg {
    fill: currentColor;
    width: 100%;
    height: 100%;
  }

  &>div {
    margin: 0 8px;
  }

  .btnPlay,
  .btnPrev,
  .btnNext,
  .volume,
  .order,
  .loop,
  .only {
    width: 32px;
    height: 32px;
    cursor: pointer;
    flex-shrink: 0;
    opacity: 0.8;
    transition: opacity 0.3s;
    &:hover {
      opacity: 1;
    }
  }

  .btnPlay {
    width: 40px;
    height: 40px;
  }
  .btnNext {
    transform: rotate(180deg);
  }

  .playerMusic {
    width: 100%;
    .playerInfo {
      display: flex;
      justify-content: space-between;
      font-size: 12px;
      margin: 0 5px 6px 5px;
      line-height: 16px;
      white-space: pre;
    }
    .playerProgress {
      margin-bottom: 0;
      margin-top: 0;
    }
  }
  .volumeBar {
    display: flex;
    align-items: center;
    .volumeProgress {
      width: 60px;
      margin: 0 0 0 4px;
    }
  }
  :global {
    .ant-slider {
      .ant-slider-rail {
        background-color: rgba(255, 255, 255, 0.1);
      }
      .ant-slider-track {
        background-color: rgba(255, 255, 255, 0.7);
      }
      .ant-slider-handle::after {
        box-shadow: none;
      }
    }
  }
}
