.coverPanel {
  height: 100%;
  width: 100%;
  position: relative;
  transition: opacity 0.3s;
  opacity: 0;
  cursor: default;
  color: rgba(225, 225, 225, 0.8);
  &.show {
    opacity: 1;
  }

  .infoCover {
    margin: auto;
    width: 186px;
    height: 186px;
    border-radius: 4px;
    background-size: cover;
  }
  .infoTitle {
    margin-top: 16px;
  }
  .infoTitle,
  .infoAuthor {
    white-space: nowrap;
    width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    height: 28px;
    line-height: 28px;
    text-align: center;
    font-size: 14px;
  }
}
