.container {
  width: 100%;
  height: 100%;
  font-size: 14px;
  position: relative;
  // color: #fff;

  .panel {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 0 10px;
  }

  .bgCover {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    .mask,
    .bgImage {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }

    .mask {
      background: linear-gradient(rgba(31, 31, 29, 0.6), rgba(0, 0, 0, 0.7));
    }

    .bgImage {
      background-size: cover;
    }
  }

  .left {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 44px;
    height: 44px;
  }

  .right {
    flex: 1;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 10px;
  }

  .icon {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: transparent;
    color: #d48806;
    filter: grayscale(1);
    transition: filter 0.2s;
  }

  .title {
    font-weight: 600;
    font-size: 18px;
    margin-bottom: 4px;
    line-height: 24px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    width: 86px;
  }

  .info {
    font-size: 12px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    width: 86px;
  }

  &:hover {
    .icon {
      animation: wobble 1s infinite;
      transform-origin: bottom center;
      filter: grayscale(0);
    }

    @keyframes wobble {
      0%, 20% {
        -webkit-transform: rotate3d(0, 0, 1, 0deg);
      }
      30% {
        -webkit-transform: rotate3d(0, 0, 1, 10deg);
      }
      50% {
        -webkit-transform: rotate3d(0, 0, 1, -10deg);
      }
      65% {
        -webkit-transform: rotate3d(0, 0, 1, 5deg);
      }
      75% {
        -webkit-transform: rotate3d(0, 0, 1, -5deg);
      }
      80% {
        -webkit-transform: rotate3d(0, 0, 1, 0deg);
      }
    }
  }
}
