.cloudDownloadPanel {
  padding: 24px 16px;
  .searchPanel {
    padding: 8px 0;
  }
  .resultPanel {
    margin-top: 8px;
    padding: 8px 0;
    max-height: 400px;
    overflow-y: auto;
    &::-webkit-scrollbar {
      display: none;
    }
    .resultItem {
      color: rgba(255, 255, 255, 0.65);
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 8px 0;
      border-top: 1px solid rgba(255, 255, 255, 0.05);
      &:first-child {
        border-top: none;
      }
      .info {
        font-size: 14px;
        width: 344px;
        padding-right: 32px;
        .author {
          font-size: 12px;
        }
        .title, .author {
          overflow: hidden;
          text-overflow:ellipsis;
          white-space: nowrap;
        }
      }
      .action {
        flex-shrink: 0;
        width: 104px;
        :global {
          .ant-btn {
            color: rgba(255, 255, 255, 0.65);
            border-color: currentColor;
            &:hover {
              color: rgba(255, 255, 255, 0.85);
              background-color: rgba(0, 0, 0, 0.06);
            }
          }
        }
      }
    }
  }
}
