.listPanel {
  height: 100%;
  width: 100%;
  position: relative;
  overflow-y: auto;

  &::-webkit-scrollbar {
    display: none;
  }

  &.show {
    .listItem {
      opacity: 1;
      transition: opacity 0.3s;
    }
  }

  .listItem {
    // height: 100vh;
    width: 100%;
    opacity: 0;
    color: rgba(255, 255, 255, 0.45);
    transition: all 0.2s;
    display: flex;
    height: 50px;
    line-height: 50px;
    font-size: 14px;
    position: relative;
    &::before, &::after {
      content: '';
      display: block;
      position: absolute;
      width: 4px;
      background-color: transparent;
      height: 40%;
      top: 50%;
      transform: translateY(-50%);
    }
    &::before {
      left: 0;
      border-radius: 0 5px 5px 0;
    }
    &::after {
      right: 0;
      border-radius: 5px 0 0 5px;
    }

    &:nth-child(2n) {
      background-color: rgba(0, 0, 0, .05);
    }

    &.highlight {
      color: rgba(255, 255, 255, 0.85) !important;
      &::before, &::after {
        background-color: rgba(255, 255, 255, 0.85);
      }
    }

    &.pointer {
      cursor: pointer;

      &:hover {
        color: rgba(255, 255, 255, 0.65);
      }
    }

    .title {
      width: 60%;
      padding: 0 12px;
    }

    .author {
      width: 40%;
      padding: 0 12px;
    }
    .duration {
      width: 74px;
      flex-shrink: 0;
      padding: 0 12px;
    }
    .num {
      width: 52px;
      text-align: right;
      flex-shrink: 0;
      padding: 0 12px;
    }

    .title,
    .author {
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }
  }
}
