.container {
  height: 100%;
  width: 100%;
  position: relative;
  color: #fff;
  overflow: hidden;

  .backdrop {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #000;
    &.emptyBackdrop {
      background-color: #ad6800;
    }

    &>div {
      filter: blur(65px);
      opacity: .6;
      transform: scale(1.5);
      transform-origin: center;
    }
  }

  .panel {
    height: 100%;
    margin: 0 7.638889%;
    padding-top: 64px;
    position: relative;
    display: flex;
    flex-direction: column;
    &.showroom {
      margin: 0 24px !important;
      padding-top: 32px !important;
    }

    .main {
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: row;
      overflow: hidden;

      .left {
        width: 100%;
        display: flex;
        flex-direction: column;

        .toolbar {
          margin-bottom: 20px;
          flex-shrink: 0;
          // padding: 0 60px;
        }

        .songList {
          height: 100%;
          overflow: auto;
          margin-bottom: 20px;
        }
      }

      .right {
        flex-shrink: 0;
        width: 340px;
        margin-left: 100px;

        .songInfo {
          display: flex;
          width: 100%;
          height: 100%;
          flex-direction: column;
          justify-content: space-between;
          .songCover {
            width: 100%;
            height: 285px;
            flex-shrink: 0;
            display: flex;
            justify-content: center;
            align-items: center;
          }
          .songLrc {
            width: 100%;
            height: 100%;
            padding: 20px 0;
            display: flex;
            align-items: center;
            overflow: hidden;
          }
        }
      }

      .lrcPanel {
        width: 100%;
        height: 100%;
      }
    }

    .footer {
      flex-shrink: 0;
      height: 72px;
      display: flex;
      align-items: center;
      .copyright {
        flex-shrink: 0;
        padding: 0px 8px;
        margin-left: 24px;
      }
    }
  }

  @media (max-width: 1300px) {
    .panel {
      margin: 0 6%;

      .main {
        .right {
          width: 300px;
          margin-left: 20px;
        }
      }
    }
  }
}
