.button {
  cursor: pointer;
  color: rgba(255, 255, 255, 0.85);
  background-color: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(10px);
  display: inline-flex;
  justify-content: center;
  align-items: center;
  line-height: 1;
  min-width: 40px;
  height: 40px;
  border-radius: 40px;
  font-size: 14px;
  transition: .2s;
  &:hover {
    opacity: 0.8;
    color: rgba(255, 255, 255, 0.95);
  }
  &:active {
    transform: scale(.92);
  }
}
