.cover {
  height: 100vh;
  width: 100vw;
  position: relative;
  &.show {
    .coverPanel {
      opacity: 1;
      transition: opacity 0.3s;
    }
  }

  .coverPanel {
    height: 100vh;
    width: 100vw;
    opacity: 0;

    &>div {
      width: 100%;
      height: 100%;
      background-size: cover;
    }
  }
}
