.lrcPanel {
  height: 100%;
  width: 100%;
  position: relative;
  transition: opacity 0.3s;
  opacity: 0;
  font-size: 14px;
  color: rgba(225, 225, 225, 0.8);
  text-align: center;
  overflow-y: auto;
  &::-webkit-scrollbar {
    display: none;
  }

  &.show {
    opacity: 1;
  }
  .empty {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
  }
  .list {
    cursor: default;
    // overflow-y: auto;
    height: 100%;
    width: 100%;
    // &::-webkit-scrollbar {
    //   display: none;
    // }
    &.middle {
      line-height: 34px;
    }
    &.large {
      line-height: 58px;
      font-size: 22px;
    }
    p.on {
      color: #d48806;
    }
  }
}
